import * as React from 'react';

import { StyledPageButton, StyledCurrentPageButton } from './styled';
import { IPagination } from './definitions';

interface IPagingProps {
  pagination: IPagination;
  search: (next: boolean, goto: number) => void;
}

const previousPages = (
  currentPage: number,
  totalPages: number,
  search: (next: boolean, goto: number) => void
): React.ReactElement<any>[] => {
  const elements: React.ReactElement<any>[] = [];
  const doUntill = currentPage <= 10 ? 0 : currentPage - 5;
  for (let i = currentPage - 1; i > doUntill; i--) {
    elements.push(
      <StyledPageButton
        className={'pintraPagingButton'}
        onClick={(event: React.MouseEvent<HTMLButtonElement>) => search(false, i)}>
        {i.toString()}
      </StyledPageButton>
    );
  }
  return elements.reverse();
};

const nextPages = (
  currentPage: number,
  totalPages: number,
  search: (next: boolean, goto: number) => void
): React.ReactElement<any>[] => {
  const doUntill =
    totalPages < 10
      ? totalPages - currentPage
      : currentPage <= 10
      ? 10 - currentPage
      : currentPage + 4 <= totalPages
      ? 4
      : totalPages - currentPage;
  const elements: React.ReactElement<any>[] = [];
  for (let i = 1; i <= doUntill; i++) {
    elements.push(
      <StyledPageButton
        className={'pintraPagingButton'}
        onClick={(event: React.MouseEvent<HTMLButtonElement>) => search(false, currentPage + i)}>
        {currentPage + i}
      </StyledPageButton>
    );
  }
  return elements;
};

export const Paging: React.SFC<IPagingProps> = props =>
  props.pagination.totalPages > 0 && (
    <React.Fragment>
      {props.pagination.hasPreviousPage && (
        <StyledPageButton
          className={'pintraPagingButtonPrevious'}
          onClick={(event: React.MouseEvent<HTMLButtonElement>) => props.search(false, -1)}>
          {'<'}
        </StyledPageButton>
      )}
      {previousPages(props.pagination.page, props.pagination.totalPages, props.search).map(
        (element: React.ReactElement<any>) => element
      )}
      <StyledCurrentPageButton className={'pintraPagingButtonCurrent'}>
        {props.pagination.page}
      </StyledCurrentPageButton>
      {nextPages(props.pagination.page, props.pagination.totalPages, props.search).map(
        (element: React.ReactElement<any>) => element
      )}
      {props.pagination.hasNextPage && (
        <StyledPageButton
          className={'pintraPagingButtonNext'}
          onClick={(event: React.MouseEvent<HTMLButtonElement>) => props.search(true, -1)}>
          {'>'}
        </StyledPageButton>
      )}
    </React.Fragment>
  );
