export class UriSegments {
  public protocol: string;
  public host: string;
  public port: string;
  public path: string[];
  public queryString: string;
}

export const getUriSegments = (uri: string): UriSegments | null => {
  if (!uri) {
    console.error('Failed to convert input string to uri segments: ' + uri);
    return null;
  }
  const uriSegments = uri.split('/');
  if (!Array.isArray(uriSegments) || uriSegments.length == 0) {
    console.error('Failed to convert input string to uri segments: ' + uri);
    return null;
  }
  if (!/^(http:|https:)/i.test(uriSegments[0])) {
    console.error('Uri did not start with supported protocol: ' + uri);
    return null;
  }
  if (uriSegments.length < 3) {
    console.error('Could not determine valid host: ' + uri);
    return null;
  }
  const result = new UriSegments();
  result.protocol = uriSegments[0].replace(':', '');
  const hostSegments = uriSegments[2].split(':');
  result.host = hostSegments[0];
  result.port = hostSegments.length == 2 ? hostSegments[1] : '';
  if (uriSegments.length == 3) {
    return result;
  }
  uriSegments.splice(0, 3);
  const pathAndQueryString = uriSegments[uriSegments.length - 1].split('?');
  result.queryString = pathAndQueryString.length == 2 ? pathAndQueryString[1] : '';
  uriSegments[uriSegments.length - 1] = pathAndQueryString[0];
  result.path = [...uriSegments];
  return result;
};
