import { Reducer } from 'redux';
import { PaginationAction, PaginationActionType } from '../actions';
import { IPagination } from '../../../common/definitions';

export const PaginationReducer: Reducer<IPagination> = (
  state: IPagination = { page: 1, hasPreviousPage: false, hasNextPage: false, totalPages: 0 },
  action: PaginationAction
) => {
  switch (action.type) {
    case PaginationActionType.UPDATE:
      if (action.pagination) {
        return { ...action.pagination };
      }
    default:
      return state;
  }
};
