import * as React from 'react';

import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { Image, ImageFit } from 'office-ui-fabric-react/lib/Image';

import { getUriSegments } from '../../../utils/PathHelpers';
import { IEnv } from '../../store';
import { IResult, ResultType } from '../common/definitions';
import {
  StyledItemCell,
  StyledItemIconWrapper,
  StyledItemIcon,
  StyledItemContent,
  StyledItemPrimaryText,
  StyledLink,
  StyledItemPath,
  StyledItemSecondaryText,
  StyledItemTertiaryText,
  StyledItemMeta
} from '../../../common/styled';
import { tryReplaceTemplateTags } from '../../../utils/htmlTemplateHelpers';
import { PersonaActionButtons } from '../../../common/PersonaActionButtons';
import { asBool } from '../../../utils/typeHelpers';
import { avatar } from '../../../common/avatar';
import { getDataUrl } from '../../../common/PersonaHelpers';

interface IListItemProps {
  item: IResult;
  index: number | undefined;
  env: IEnv;
}

interface IListItemState {
  dataUrl: string;
  primaryHtml: string;
  secondaryHtml: string;
  tertiaryHtml: string;
  metaHtml: string;
}

export class ListItem extends React.Component<IListItemProps, IListItemState> {
  public constructor(props: IListItemProps) {
    super(props);
    const { env, item } = this.props;
    this.state = {
      dataUrl: avatar,
      primaryHtml: '',
      secondaryHtml: '',
      tertiaryHtml: '',
      metaHtml: ''
    };
    let userName = '';
    if ('UserName' in item) {
      userName = item.UserName;
      getDataUrl(item.UserName, env, (dataUrl: string) => this.setState({ dataUrl }));
    }
    tryReplaceTemplateTags(env.props.primaryHtml, item, env as any, userName)
      .then((primaryHtml: string) => this.setState({ primaryHtml }))
      .catch(err => console.log(err));
    tryReplaceTemplateTags(env.props.secondaryHtml, item, env as any, userName)
      .then((secondaryHtml: string) => this.setState({ secondaryHtml }))
      .catch(err => console.log(err));
    tryReplaceTemplateTags(env.props.tertiaryHtml, item, env as any, userName)
      .then((tertiaryHtml: string) => this.setState({ tertiaryHtml }))
      .catch(err => console.log(err));
    tryReplaceTemplateTags(env.props.metaHtml, item, env as any, userName)
      .then((metaHtml: string) => this.setState({ metaHtml }))
      .catch(err => console.log(err));
  }

  public componentDidMount(): void {
    const { item, env } = this.props;
    if ('UserName' in item) {
    }
  }

  public render(): React.ReactElement<IListItemProps> {
    const { env, item } = this.props;
    const { dataUrl, primaryHtml, secondaryHtml, tertiaryHtml, metaHtml } = this.state;
    const uriSegments = getUriSegments(item.Path);
    const baseUrl = uriSegments
      ? uriSegments.protocol + '://' + uriSegments.host + (uriSegments.port ? ':' + uriSegments.port : '')
      : '';
    return (
      <StyledItemCell className={'pintraItemCell'}>
        <StyledItemIconWrapper className={'pintraItemWrapper'}>
          <StyledItemIcon className={'pintraItemIcon'}>
            {item.typeInfo.type === ResultType.user ? (
              <Image className={'pintraItemIcon'} src={dataUrl} width={50} height={50} imageFit={ImageFit.cover} />
            ) : (
              <Icon iconName={item.icon} />
            )}
          </StyledItemIcon>
        </StyledItemIconWrapper>
        <StyledItemContent className={'pintraItemContent'}>
          <StyledItemPrimaryText className={'pintraItemPrimaryText'}>
            <StyledLink href={item.Path} target={'_blank'} dangerouslySetInnerHTML={{ __html: primaryHtml }} />
          </StyledItemPrimaryText>
          {'Path' in item && item.typeInfo.type !== ResultType.user && (
            <StyledItemPath className={'pintraItemPath'}>
              <StyledLink href={baseUrl} target={'_blank'}>
                {'[...]'}
              </StyledLink>
              {uriSegments && uriSegments.path ? (
                uriSegments.path.map((pathSegment: string, index: number) => {
                  if (index + 1 == uriSegments.path.length) {
                    return (
                      <React.Fragment>
                        {'/'}
                        <StyledLink href={baseUrl + '/' + uriSegments.path.join('/')} target={'_blank'}>
                          {uriSegments.path[index]}
                        </StyledLink>
                      </React.Fragment>
                    );
                  }
                  const sliceOfPath = uriSegments.path.slice(0, index + 1);
                  return (
                    <React.Fragment>
                      {'/'}
                      <StyledLink href={baseUrl + '/' + sliceOfPath.join('/')} target={'_blank'}>
                        {uriSegments.path[index]}
                      </StyledLink>
                    </React.Fragment>
                  );
                })
              ) : (
                <StyledLink href={item.Path}>{item.Path}</StyledLink>
              )}
            </StyledItemPath>
          )}
          <StyledItemSecondaryText
            className={'pintraItemSecondaryText'}
            dangerouslySetInnerHTML={{ __html: secondaryHtml }}
          />
          <StyledItemTertiaryText
            className={'pintraTertiaryText'}
            dangerouslySetInnerHTML={{ __html: tertiaryHtml }}
          />
          <StyledItemMeta className={'pintraItemMeta'} dangerouslySetInnerHTML={{ __html: metaHtml }} />
          {item.typeInfo.type === ResultType.user && asBool(env.props.showContactActionButtons) && (
            <PersonaActionButtons
              emailProperty={env.props.emailProperty}
              imProperty={env.props.imProperty}
              workPhoneProperty={env.props.workPhoneProperty}
              mobilePhoneProperty={env.props.mobilePhoneProperty}
              item={item}
            />
          )}
        </StyledItemContent>
      </StyledItemCell>
    );
  }
}
