import * as React from 'react';

import { ThunkDispatch } from 'redux-thunk';

import { SearchThunk } from '../../store/actions/SearchThunk';
import { connect } from 'react-redux';
import { IPagination } from '../../../common/definitions';
import { IContentBySearchState } from '../../store';
import { Paging } from '../../../common/Paging';

interface IPropsFromState {
  pagination: IPagination;
}

interface IPropsFromDispatch {
  search: (next: boolean, goto: number) => void;
}

interface IOwnProps {}

const mapStateToProps = (state: IContentBySearchState) => ({
  pagination: state.pagination
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, any>, OwnProps: IOwnProps) => ({
  search: (next: boolean, goto: number = -1) => dispatch(SearchThunk(false, next, goto))
});

export const _Paging = connect<IPropsFromState, IPropsFromDispatch, IOwnProps>(
  mapStateToProps,
  mapDispatchToProps
)(Paging);
