/**
 * Imports
 */

import * as React from 'react';
import { connect } from 'react-redux';

import { FocusZone, FocusZoneDirection } from 'office-ui-fabric-react/lib/FocusZone';
import { List } from 'office-ui-fabric-react/lib/List';
import { initializeIcons } from 'office-ui-fabric-react/lib/Icons';

import { ListItem } from './ListItem';

import { IEnv, IContentBySearchState } from '../../store';
import { EnvUpdateAction } from '../../store/actions/EnvUpdateAction';
import { ThunkAction } from 'redux-thunk';
import { QueryUpdatedThunk } from '../../store/actions/QueryUpdatedThunk';
import { asBool } from '../../../utils/typeHelpers';
import { _ModernSearchBox } from './_ModernSearchBox';
import { IResult, ResultType } from '../common/definitions';
import { _Notifications } from './_Notifications';
import { _Paging } from './_Paging';
import { StyledFooter } from '../../../common/styled';

initializeIcons(/* optional base url */);

interface IPropsFromState {
  items: IResult[];
}

interface IPropsFromDispatch {
  importEnv: () => void;
  query: (query: string) => ThunkAction<any, any, any, any>;
}

interface IOwnProps {
  env: IEnv;
}

export interface IListProps {
  env: IEnv;
  query: (query: string) => void; // thunk
  items: IResult[];
}

export interface IListState {}

export class _List extends React.Component<IListProps, IListState> {
  public constructor(props: IListProps) {
    super(props);
    const { env } = this.props;
    asBool(env.props.autoSearch) === true && this.props.query('');
  }

  public render(): React.ReactElement<IListProps> {
    const { items, env } = this.props;
    return (
      <React.Fragment>
        {asBool(env.props.hideSearchBox) === false && <_ModernSearchBox />}
        {asBool(env.props.hideMessageBar) === false && <_Notifications />}
        <FocusZone direction={FocusZoneDirection.vertical}>
          <List items={items} onRenderCell={this.renderListItem} className={'pintraCbsList'} />
        </FocusZone>
        <StyledFooter>{asBool(env.props.hidePaging) === false && <_Paging />}</StyledFooter>
      </React.Fragment>
    );
  }

  private renderListItem = (item: IResult, index: number | undefined) => {
    const { env } = this.props;
    return <ListItem item={item} index={index} env={env} />;
  };
}

const mapStateToProps = (state: IContentBySearchState) => ({
  items: state.items
});

const mapDispatchToProps = (dispatch: any, ownProps: IOwnProps) => ({
  importEnv: () => {
    dispatch(EnvUpdateAction(ownProps.env));
  },
  query: (query: string) => dispatch(QueryUpdatedThunk(query))
});

const mergeProps = (stateProps: IPropsFromState, dispatchProps: IPropsFromDispatch, ownProps: IOwnProps) => {
  dispatchProps.importEnv();
  return {
    ...stateProps,
    query: dispatchProps.query,
    ...ownProps
  };
};

export const ResultList = connect<IPropsFromState, IPropsFromDispatch, IOwnProps>(
  mapStateToProps,
  mapDispatchToProps,
  mergeProps
)(_List) as React.ComponentClass<IOwnProps> & {
  WrappedComponent: React.ComponentType<IListProps>;
};
