import { IItemsUpdateAction, ItemsActionType } from '.';
import { ActionCreator, Action } from 'redux';
import { IContentBySearchState } from '..';
import { ThunkAction, ThunkDispatch } from 'redux-thunk';
import { ItemsUpdatedAction } from './ItemsUpdatedAction';
import { IResult } from '../../components/common/definitions';

/**
 * User initiates a search
 *
 * note: ThunkAction type parameters:
 * R = Return Type of the inner function
 * S = State (from getState())
 * E = Extra parameter
 * A = Action
 *
 * @param items
 */

export const ItemsUpdatedThunk: ActionCreator<ThunkAction<void, IContentBySearchState, void, Action>> = (
  items: IResult[]
) => {
  return (
    dispatch: ThunkDispatch<IContentBySearchState, void, Action>,
    getState: () => IContentBySearchState
  ): void => {
    const state = getState();
    dispatch(ItemsUpdatedAction(items));
  };
};
