import { ActionCreator, Action } from 'redux';
import { ThunkAction, ThunkDispatch } from 'redux-thunk';

import { isNumber } from 'lodash';

import { IContentBySearchState, IEnv } from '..';
import { QueryUpdatedAction } from './QueryUpdatedAction';
import { SearchThunk } from './SearchThunk';
import { ItemsUpdatedThunk } from './ItemsUpdatedThunk';
import { PaginationUpdatedAction } from './PaginationUpdatedAction';
import { StatusType } from '../../../common/definitions';
import { StatusChangedAction } from '../../../common/redux/Status';
import { asBool, getNumberOrDefault } from '../../../utils/typeHelpers';

/**
 * User initiates a search
 *
 * note: ThunkAction type parameters:
 * R = Return Type of the inner function
 * S = State (from getState())
 * E = Extra parameter
 * A = Action
 *
 * @param query
 */
export const QueryUpdatedThunk: ActionCreator<ThunkAction<void, IContentBySearchState, void, Action>> = (
  query: string
) => {
  return (
    dispatch: ThunkDispatch<IContentBySearchState, void, Action>,
    getState: () => IContentBySearchState
  ): void => {
    const { env } = getState();
    let minQueryLength = getNumberOrDefault(env.props.minQueryLength, 3);
    dispatch(QueryUpdatedAction(query));

    // If query has been deleted then reset the application to its initial state
    if (!query) {
      dispatch(StatusChangedAction({ type: StatusType.READY }));
      dispatch(ItemsUpdatedThunk([]));
      dispatch(
        PaginationUpdatedAction({
          page: 1,
          hasNextPage: false,
          hasPreviousPage: false,
          totalPages: 0 // totalPages 0 will hide the pagination
        })
      );
    }

    if (!asBool(env.props.autoSearch) && query.length < minQueryLength) {
      return;
    }

    // User clicked search button or query >= min req. length
    let timer = (window as any)['spoQueryUpdatedTimer'];
    if (isNumber(timer)) {
      clearTimeout(timer);
    }
    (window as any)['spoQueryUpdatedTimer'] = setTimeout(() => {
      dispatch(SearchThunk(true, true));
    }, 1000);
  };
};
