import Axios from 'axios';
import { IToken, TokenRequestError, TokenCache, TokenRequestV2 } from 'pintra-fx';
import { IEnv } from '../store';
import { getResultTypeInfo } from '../components/common/ResultTypeHelpers';
import { getIcon } from '../components/common/IconHelpers';
import { asBool } from '../../utils/typeHelpers';
import { IWrappedResults, IResult, ResultType } from '../components/common/definitions';
import { string } from 'prop-types';
import moment = require('moment');

export class SharePointService {
  public static async fetch(env: IEnv, query: string, page: number) {
    let pageSize = env.props.pageSize && env.props.pageSize.length > 0 ? parseInt(env.props.pageSize) : 10;
    let startRow = page * pageSize - pageSize;
    let home = env.props.home && env.props.home.length > 0 ? env.props.home : '';
    let resultSourceId =
      env.props.resultSourceId && env.props.resultSourceId.length > 0
        ? env.props.resultSourceId
        : '8413cd39-2156-4e00-b54d-11efd9abdb89';
    let extraSelectProperties =
      env.props.selectProperties && env.props.selectProperties.length > 0
        ? env.props.selectProperties.split('|').join(',')
        : '';
    let selectProperties = `&selectproperties='HitHighlightedSummary,Author,Title,Description,Path,ContentClass,FileExtension,LastModifiedTime,IsDocument,ContentTypeId,${extraSelectProperties}'`;
    let sortList = env.props.sortList && env.props.sortList.length > 0 ? `&sortlist='${env.props.sortList}'` : '';
    let clientType = `&clientType='ContentSearchRegular'`;
    let queryTemplate =
      env.props.queryTemplate && env.props.queryTemplate.length > 0 ? env.props.queryTemplate : '{searchterms}';

    const tokenResponse: IToken | TokenRequestError = await TokenCache.getTokenV2(
      new TokenRequestV2('sharepointSitesSearchAll', `${env.props.home}/Sites.Search.All`),
      {
        wpAjaxAdminUrl: env.wpAjaxAdminUrl,
        nonce: env.nonce
      }
    );

    if (tokenResponse instanceof Error) {
      return new Error('Unexpected: Could not retrieve a valid access token.');
    }

    const token = <IToken>tokenResponse;

    const keywords = asBool(env.props.addWildcard) ? SharePointService.addWildcard(query) : query;

    const response = await Axios.get(
      `${home}/_api/search/query?querytext='${keywords}'${clientType}${sortList}&querytemplate='${queryTemplate}'${selectProperties}&startrow=${startRow}&rowlimit=${pageSize}&Properties='ClientFunction:AllResults,EnableDynamicGroups:true'&sourceid='${resultSourceId}'`,
      {
        headers: {
          Authorization: `Bearer ${token.bearer}`,
          Accept: 'application/json;odata=verbose'
        }
      }
    );

    return SharePointService.searchResultsParser(response);
  }

  public static searchResultsParser(results: any): IWrappedResults {
    try {
      const parsed = results.data.d.query.PrimaryQueryResult.RelevantResults.Table.Rows.results.map((row: any) => {
        let raw: { [key: string]: string } = {};
        row.Cells.results.map((item: any) => {
          raw[<string>item['Key']] = item['Value'];
        });

        let typeInfo = getResultTypeInfo(raw);

        let result: IResult = {
          ...raw,
          typeInfo,
          icon: getIcon(typeInfo.type),
          primaryUser:
            typeInfo.type === ResultType.user && SharePointService.hasProp(raw, 'PreferredName')
              ? raw.PreferredName
              : SharePointService.hasProp(raw, 'Author')
              ? raw.Author
              : '',
          lastModified: SharePointService.hasProp(raw, 'LastModifiedTime')
            ? moment(raw.LastModifiedTime).format('DD/MM/YYYY HH:mm')
            : ''
        };

        return result;
      });
      return {
        results: parsed,
        totalRows: parseInt(results.data.d.query.PrimaryQueryResult.RelevantResults.TotalRows)
      };
    } catch (err) {
      console.log(err);
      return { results: [], totalRows: 0 };
    }
  }

  private static addWildcard = (query: string): string => {
    if (!query) {
      return '';
    }
    const keywords = query.split(' ');
    const result = keywords.map(element => {
      return /\*$/.test(element) ? element : (element += '*');
    });

    return result.join(' ');
  };

  private static hasProp(obj: any, propName: string): boolean {
    if (propName in obj && obj[propName] != null) {
      return true;
    }
    return false;
  }
}
