import { ResultType } from './definitions';

export const getIcon = (resultType: ResultType): string => {
  switch (resultType) {
    case ResultType.wordDocument:
      return 'WordDocument';
    case ResultType.excelDocument:
      return 'ExcelDocument';
    case ResultType.powerPointDocument:
      return 'PowerPointDocument';
    case ResultType.image:
      return 'FileImage';
    case ResultType.list:
      return 'ViewList';
    case ResultType.library:
      return 'DocLibrary';
    case ResultType.workspace:
      return 'SharePointLogo';
    case ResultType.video:
      return 'Video';
    case ResultType.link:
      return 'Link';
    case ResultType.pdfDocument:
      return 'PDF';
    default:
      return 'Globe';
  }
};
