/**
 * Shape for a simplified (SharePoint) search result
 */
export interface IResult {
  icon: string;
  typeInfo: ResultTypeInfo;
  primaryUser: string;
  [key: string]: any;
}

export interface IWrappedResults {
  results: IResult[];
  totalRows: number;
}

export enum ResultType {
  aspxDocument = 'aspxDocument',
  wikiPage = 'wikiPage',
  excelDocument = 'excelDocument',
  wordDocument = 'wordDocument',
  powerPointDocument = 'powerPointDocument',
  unkownDocumentType = 'unkownDocumentType',
  unknown = 'unknown',
  pdfDocument = 'pdfDocument',
  image = 'image',
  video = 'video',
  link = 'link',
  user = 'user',
  workspace = 'workspace',
  list = 'list',
  library = 'library',
  task = 'task',
  event = 'event',
  news = 'news',
  folder = 'folder',
  newsFeed = 'newsFeed'
}

export interface ResultTypeInfo {
  type: ResultType;
  isDocument: boolean;
  fileExtension: string;
}
