import * as React from 'react';

import { connect } from 'react-redux';
import { QueryUpdatedThunk } from '../../store/actions/QueryUpdatedThunk';
import { ThunkDispatch, ThunkAction } from 'redux-thunk';
import { IStatus } from '../../../common/definitions';
import { IContentBySearchState } from '../../store';
import { ModernSearchBox } from '../../../common/ModernSearchBox';

interface IPropsFromState {
  status: IStatus;
}

interface IPropsFromDispatch {
  query: (query: string) => ThunkAction<any, any, any, any>;
}

interface IOwnProps {}

const mapStateToProps = (state: IContentBySearchState) => ({
  status: state.status
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, any>, ownProps: IOwnProps) => ({
  query: (query: string) => dispatch(QueryUpdatedThunk(query))
});

export const _ModernSearchBox = connect<IPropsFromState, IPropsFromDispatch, IOwnProps>(
  mapStateToProps,
  mapDispatchToProps
)(ModernSearchBox);
