import { ResultTypeInfo, ResultType } from './definitions';

export const getResultTypeInfo = (result: { [key: string]: string }): ResultTypeInfo => {
  const isDocument =
    hasProp(result, 'IsDocument') &&
    result.IsDocument &&
    JSON.parse(result.IsDocument) &&
    hasProp(result, 'ContentTypeId') &&
    result.ContentTypeId.indexOf('0x0101', 0) >= 0;

  const fileExtension =
    isDocument && hasProp(result, 'FileExtension') && result.FileExtension ? result.FileExtension : '';

  let type = ResultType.unknown;

  if (hasProp(result, 'ContentClass') && result.ContentClass.toLowerCase() == 'urn:content-class:spspeople') {
    type = ResultType.user;
  }
  if (hasProp(result, 'ContentClass') && (result.ContentClass == 'STS_Site' || result.ContentClass == 'STS_Web')) {
    type = ResultType.workspace;
  }
  if (hasProp(result, 'ContentTypeId') && (<string>result.ContentTypeId).indexOf('0x0120', 0) >= 0) {
    type = ResultType.folder;
  }
  if (hasProp(result, 'ContentClass') && result.ContentClass.indexOf('STS_List_', 0) >= 0) {
    type = ResultType.list;
  }
  if (hasProp(result, 'Path') && result.Path.indexOf('newsfeed.aspx', 0) >= 0) {
    type = ResultType.newsFeed;
  }
  if (isDocument) {
    switch (fileExtension) {
      case 'pdf':
        type = ResultType.pdfDocument;
        break;
      case 'doc':
        type = ResultType.wordDocument;
        break;
      case 'docx':
        type = ResultType.wordDocument;
        break;
      case 'xsl':
        type = ResultType.excelDocument;
        break;
      case 'xslx':
        type = ResultType.excelDocument;
        break;
      case 'ppt':
        type = ResultType.powerPointDocument;
        break;
      case 'pptx':
        type = ResultType.powerPointDocument;
        break;
      case 'png':
        type = ResultType.image;
        break;
      case 'jpg':
        type = ResultType.image;
        break;
      case 'gif':
        type = ResultType.image;
        break;
      case 'bmp':
        type = ResultType.image;
        break;
      case 'tiff':
        type = ResultType.image;
        break;
      case 'avi':
        type = ResultType.video;
        break;
      case 'mov':
        type = ResultType.video;
        break;
      case 'mp4':
        type = ResultType.video;
        break;
      case 'aspx':
        type = ResultType.aspxDocument;
        break;
      default:
        type = type;
    }
  }
  return {
    type,
    isDocument,
    fileExtension
  };
};

const hasProp = (obj: { [key: string]: string }, propName: string): boolean => {
  if (propName in obj && obj[propName] != null) {
    return true;
  }
  return false;
};
