import { ActionCreator, Action } from 'redux';
import { IContentBySearchState } from '..';
import { ThunkAction, ThunkDispatch } from 'redux-thunk';
import { SharePointService } from '../../services/SharePointService';
import { PaginationUpdatedAction } from './PaginationUpdatedAction';
import { ItemsUpdatedThunk } from './ItemsUpdatedThunk';
import { QueryUpdatedAction } from './QueryUpdatedAction';
import { IWrappedResults } from '../../components/common/definitions';
import { StatusType } from '../../../common/definitions';
import { StatusChangedAction } from '../../../common/redux/Status';

/**
 * User initiates a search
 *
 * note: ThunkAction type parameters:
 * R = Return Type of the inner function
 * S = State (from getState())
 * E = Extra parameter
 * A = Action
 *
 * @param query
 */
export const SearchThunk: ActionCreator<ThunkAction<void, IContentBySearchState, void, Action>> = (
  restart: boolean,
  next: boolean,
  goto: number = -1
) => {
  return (
    dispatch: ThunkDispatch<IContentBySearchState, void, Action>,
    getState: () => IContentBySearchState
  ): void => {
    const { query, pagination, env } = getState();
    let page = restart ? 1 : next ? pagination.page + 1 : goto > -1 ? goto : pagination.page - 1;
    dispatch(StatusChangedAction({ type: StatusType.FETCHING }));
    dispatch(ItemsUpdatedThunk([]));
    dispatch(
      PaginationUpdatedAction({
        page: 1,
        hasNextPage: false,
        hasPreviousPage: false,
        totalPages: 0 // totalPages 0 will hide the pagination
      })
    );
    SharePointService.fetch(env, query, page)
      .then((wrappedResult: IWrappedResults) => {
        dispatch(ItemsUpdatedThunk(wrappedResult.results));
        const pageSize = parseInt(env.props.pageSize);
        dispatch(
          PaginationUpdatedAction({
            page: page,
            hasNextPage: wrappedResult.results.length == pageSize,
            hasPreviousPage: page > 1,
            totalPages: wrappedResult.totalRows > 0 ? Math.ceil(wrappedResult.totalRows / pageSize) : 0
          })
        );
        if (!wrappedResult.totalRows || wrappedResult.totalRows == 0) {
          dispatch(StatusChangedAction({ type: StatusType.NORESULTS, statusMessage: 'No results' }));
        }
        if (wrappedResult.totalRows > 0) {
          dispatch(
            StatusChangedAction({
              type: StatusType.HASRESULTS,
              statusMessage: `About ${wrappedResult.totalRows} results`
            })
          );
        }
      })
      .catch((error: any) => {
        console.log(error);
        let errorMessage =
          'Unknown error occured. Please try and refresh the page. If the issue persists, please contact your system administrator.';
        if ('response' in error && 'status' in error.response) {
          if (error.response.status == 401) {
            errorMessage =
              'Your session has expired. Please refresh the page. If the issue persists, please contact your system administrator.';
          }
        } else if ('request' in error && 'status' in error.request) {
          if (error.request.status == 401) {
            errorMessage =
              'Your session has expired. Please refresh the page. If the issue persists, please contact your system administrator.';
          }
        }
        dispatch(
          StatusChangedAction({
            type: StatusType.ERROR,
            statusMessage: errorMessage
          })
        );
        dispatch(QueryUpdatedAction(''));
      });
  };
};
